<template>
  <div class="article">
    <el-page-header @back="goBack" content="详情页面"> </el-page-header>

    <div class="article_top">
      <div class="top1">
        <a-descriptions :title="data.title">
          <a-descriptions-item label="作者">
            {{ data.author }}
          </a-descriptions-item>
          <a-descriptions-item label="时间">
            {{ data.time }}
          </a-descriptions-item>
          <a-descriptions-item label="标签">
            {{ data.label }}
          </a-descriptions-item>
          <a-descriptions-item label="分类">
            {{ data.category }}
          </a-descriptions-item>

          <a-descriptions-item label="点赞">
            {{ data.star }}
          </a-descriptions-item>
          <a-descriptions-item label="阅读">
            {{ data.hits }}
          </a-descriptions-item>
        </a-descriptions>
      </div>

      <div class="content">
        {{ data.content }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: 0,
      data: {},
    };
  },
  methods: {
    goBack() {
      console.log("go back");
      window.history.back(-1);
    },

    getNewsList() {
      this.$axios
        .get("getNewsDetail", {
          params: {
            id: this.id,
          },
        })
        .then((re) => {
          this.data = re.data;
          console.log(this.data);
        });
    },
  },

  mounted() {
    this.id = this.$route.params.id;
    this.getNewsList();

    console.log(this.data);
  },
};
</script>

<style>
.article {
  width: 1100px;
  height: 1000px;
  margin: 5px auto;
}

.article .article_top {
  width: 900px;
  height: 100%;
  margin: 50px auto;
}

.article_top .top1 {
  border-bottom: 1px solid #d6d9de;
}

.article_top .content {
  margin: 20px;
}
</style>
